#container-main{
  background-color:gray;
}
#container-main>div:nth-child(odd){}
#header{
    z-index: 3;
    position:fixed;
    background-color:yellow;
    width:100vw; 
}
#header>div{
  padding: 10px 15vw;
    width:100vw; 
}
.displaynone{
  display: none;
}
.op{
    opacity: 0.6;
}
#scroll-top{
  position: fixed;
  right: 10vw;
  bottom: 0px;
  margin: 5px;
}
#body>div{
    background-color: black;
}
</style>